import Button from '../../elements/button/Button';
import Icon from '../../elements/icon/Icon';
import Line from '../../elements/line/Line';
import styles from './about-us.module.scss';

const AboutUs = () => {
  return (
    <div id='aboutUs' className='contentContainer'>
      <div className={styles.aboutUs}>
        <Line />
        <h2 className={styles.title}>
          YOU HAVE
          <div className={styles.span}>
            a lot
            <div className={styles.iconContainer}>
              <Icon class={styles.icon} iconName='circle' />
            </div>
          </div>
          GOING ON.
        </h2>
        <Line />
        <div className={styles.contentContainer}>
          <div className={styles.nameContainer}>
            <div className={styles.squareIconContainer}>
              <Icon iconName='square' />
            </div>
            <div className={styles.name}>ABOUT US</div>
          </div>
          <div className={styles.content}>
            <h3 className={styles.contentTitle}>
              <span>Running a business means</span>
              <span>wearing a lot of hats.</span>
            </h3>
            <div className={styles.textContainer}>
              <div className={styles.buttonContainer}>
                <Button text='CONTACT US'/>
              </div>
              <div className={styles.text}>
                Do you have a million ideas for growth, but don’t have the
                bandwidth to turn them into a reality?
              </div>
              <div className={styles.text}>
                That’s where we come in. We want to create a strategic
                communication plan and save you time by executing it for you.
                You need to do what you do best: focus on the parts of your
                entrepreneurial journey that inspire you. Leave the rest to us.
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AboutUs;
