import { HashLink } from 'react-router-hash-link';
import styles from './nav.module.scss';
import { nav } from './../../assets/infos';

const Nav = (props) => {
  return (
    <div className={props.class}>
      {nav.map((link) => (
        <HashLink className={props.link} smooth to={link.id}>
          {link.name}
        </HashLink>
      ))}
    </div>
  );
};

export default Nav;
