import styles from './services.module.scss';
import { services } from '../../assets/infos';
import Button from './../../elements/button/Button';

const Services = () => {
  return (
    <div id='services' className='contentsContainer'>
      <div className={styles.services}>
        {services.map((service) => (
          <div className={styles.card}>
            <h4 className={styles.pretitle}>{service.pretitle}</h4>
            <h3 className={service.id === 1 ? styles.titleFirst : styles.title}>
              {service.title}
            </h3>
            <div className={styles.imageContainer}>
              <img src={service.image} alt={service.title} />
            </div>
            <Button text='TELL ME MORE' />
          </div>
        ))}
      </div>
    </div>
  );
}

export default Services;
