import Button from '../../elements/button/Button';
import Logo from '../../elements/logo/Logo';
import Nav from '../nav/Nav';
import styles from './header.module.scss';
import Icon from './../../elements/icon/Icon';
import classNames from 'classnames';
import Line from '../../elements/line/Line';
import { useState } from 'react';

const Header = () => {
  const [mobile, setMobile] = useState(false);

  return (
    <div className={styles.headerWrapper}>
      {mobile ? (
        <div className={styles.mobileMenu}>
          <Nav class={styles.navVertical} link={styles.linkVertical} />
          <div
            onClick={() => setMobile(false)}
            className={!mobile ? styles.cross : styles.crossVisible}
          >
            <Icon iconName='cross' />
          </div>
        </div>
      ) : (
        <div className='contentContainer'>
          <div className={styles.header}>
            <Logo />
            <div className={styles.navContainer}>
              <Nav class={styles.nav} link={styles.link} />
            </div>
            <div className={styles.buttonGroup}>
              <a
                href='https://www.instagram.com/'
                className={styles.iconContainer}
              >
                <Icon class={styles.instagram} iconName='instagram' />
              </a>
              <Button text="LET'S CHAT" />
            </div>
            <div onClick={() => setMobile(true)} className={styles.burger}>
              <Icon iconName='burger' />
            </div>
          </div>
          <Line />
        </div>
      )}
    </div>
  );
};

export default Header;
