import Line from '../../elements/line/Line';
import styles from './banner.module.scss';
import card from './../../assets/images/card.png';
import Icon from '../../elements/icon/Icon';

const Banner = () => {
  return (
    <div className='contentContainer'>
      <Line />
      <div className={styles.banner}>
        <div className={styles.imageBlock}>
          <div className={styles.notice}>WEBSITE</div>
          <div className={styles.imageContainer}>
            <img src={card} alt='card' />
          </div>
        </div>
        <div className={styles.descriptionBlock}>
          <div className={styles.description}>
            <div className={styles.iconContainer}>
              <Icon iconName='star' />
            </div>
            <span>FEATURED PROJECT</span>
          </div>
          <div className={styles.description}>KATE ECKSTEIN DESIGN</div>
          <div className={styles.description}>STRATEGY, CONTENT, DESIGN, DEVELOPMENT</div>
        </div>
      </div>
      <Line />
    </div>
  );
};

export default Banner;
