import Icon from '../../elements/icon/Icon';
import styles from './our-process.module.scss';
import { ourProcesses } from '../../assets/infos';
import Button from '../../elements/button/Button';
import Line from '../../elements/line/Line';

const OurProcess = () => {
  return (
    <div className='contentContainer'>
      <div className={styles.ourProcess}>
        <div className={styles.nameContainer}>
          <div className={styles.iconContainer}>
            <Icon iconName='star-2' />
          </div>
          <div className={styles.name}>OUR PROCESS</div>
        </div>
        <div className={styles.container}>
          {ourProcesses.map((ourProcess) => (
            <div className={styles.content}>
              <div className={styles.number}>{ourProcess.number}</div>
              <div className={styles.dotsIconContainer}>
                <Icon iconName='dots' />
              </div>
              <div className={styles.card}>
                <div className={styles.titleContainer}>
                  <h4 className={styles.title}>{ourProcess.title}</h4>
                </div>
                <p className={styles.description}>{ourProcess.description}</p>
                <div className={styles.textContainer}>
                  <p className={styles.text}>{ourProcess.text}</p>
                  <div className={styles.buttonContainer}>
                    <Button text='TRY OUT OUR TEAM' />
                  </div>
                </div>
              </div>
              <div className={styles.line}>
                <Line />
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default OurProcess;
