import Button from '../../elements/button/Button';
import Icon from '../../elements/icon/Icon';
import styles from './contacts.module.scss';
import { contacts } from '../../assets/infos';
import Line from './../../elements/line/Line';

const Contacts = () => {
  return (
    <div className={styles.contactsWrapper}>
      <Line />

      <div className={styles.contactContainer}>
        <div className={styles.description}>
          <div className={styles.littleTitleContainer}>
            <div className={styles.iconContainer}>
              <Icon iconName='chat-bubble' />
            </div>
            <div className={styles.littleTitle}>CONTACT US</div>
          </div>
          <h2 className={styles.title}>
            LET'S WORK{' '}
            <span className={styles.italic}>
              together{' '}
              <div className={styles.circleIconContainer}>
                <Icon iconName='circle-4' />
              </div>
            </span>
          </h2>
          <div className={styles.buttonContainer}>
            <Button text='CONTACT US' />
          </div>
        </div>
        <div className={styles.contacts}>
          {contacts.map((contact) => (
            <div className={styles.contact}>
              <div className={styles.contactTitle}>{contact.title}</div>
              <div className={styles.contactDescription}>
                {contact.description}
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Contacts;
