import Button from '../../elements/button/Button';
import Icon from '../../elements/icon/Icon';
import Line from '../../elements/line/Line';
import styles from './selected-work.module.scss';
import selectedOne from './../../assets/images/selected-1.png';
import selectedTwo from './../../assets/images/selected-2.png';
