import styles from './last-section.module.scss';
import { social } from '../../assets/infos';
import Icon from '../../elements/icon/Icon';
import { HashLink } from 'react-router-hash-link';

const LastSection = () => {
  return (
    <div className={styles.lastSection}>
      <div className={styles.logoText}>PIPI-POOPOO® ©2022</div>
      <div className={styles.socialMedia}>
        {social.map((social) => (
          <a className={styles.socialLink} href={social.link}>
            {social.name}
          </a>
        ))}
      </div>
      <HashLink smooth to='#top' className={styles.linkContainer}>
        <div className={styles.linkText}>Back to top</div>
        <div className={styles.iconContainer}>
          <Icon iconName='arrow' />
        </div>
      </HashLink>
    </div>
  );
};

export default LastSection;
