import Button from '../../elements/button/Button';
import styles from './audit.module.scss';
import woman from './../../assets/images/woman.png';
import Icon from '../../elements/icon/Icon';
import classNames from 'classnames';

const Audit = () => {
  return (
    <div className={classNames('contentContainer', styles.auditContainer)}>
      <div className={styles.card}>
        <div className={styles.description}>
          {' '}
          <div className={styles.littleTitleContainer}>
            <div className={styles.iconContainer}>
              <Icon iconName='star' />
            </div>
            <div className={styles.littleTitle}>GET IN TOUCH</div>
          </div>
          <h2 className={styles.title}>
            SCHEDULE{' '}
            <span className={styles.italic}>
              a free{' '}
              <div className={styles.circleIconContainer}>
                <Icon iconName='circle-3' />
              </div>
            </span>
            BRAND AUDIT
          </h2>
          <p className={styles.text}>
            Get expert eyes on your brand + website. One of our creative
            strategies will take a look at your brand and give you your best
            next steps to improve your online presence to increase conversions.
            From there, we will offer you the next steps on how to engage our
            team, or you can take our free advice and grow your business
            yourself.
          </p>
          <label className={styles.inputContainer}>
            <input placeholder='Email' className={styles.input} type='text' />
            <div className={styles.buttonContainer}>
              <Button text='SUBMIT' />
            </div>
          </label>
        </div>
        <div className={styles.imageContainer}>
          <img src={woman} alt='woman' />
        </div>
      </div>
    </div>
  );
};

export default Audit;
